.hero {
  position: relative;
  min-height: 600px;
  padding: 360px 0 90px 0;
  @include big-desktop {
    padding: 280px 0 40px 0;
  }
  @include tablet {
    padding: 250px 0 40px 0;
  }
  @include mobile {
    padding: 200px 0 40px 0;
  }
  @media (max-width: 480px) {
    padding: 170px 0 40px 0;
  }

  &__bg {
    position: absolute;
    inset: 0;
    img {
      position: absolute;
      inset: 0;
      width: 100%;
    }
  }
  &__decor {
    position: absolute;
    z-index: 1;
  }
  &__decor1 {
    width: 340px;
    height: 340px;
    top: 1000px;
    left: 0;
  }
  &__decor2 {
    width: 340px;
    height: 340px;
    top: 1200px;
    right: 0;
  }
  &__decor3 {
    width: 440px;
    height: 440px;
    top: 1900px;
    left: 0;
  }
  &__decor4 {
    width: 440px;
    height: 440px;
    top: 2100px;
    right: 0;
  }
  &__decor5 {
    width: 340px;
    height: 340px;
    top: 2700px;
    left: 0;
  }
  &__decor6 {
    width: 340px;
    height: 340px;
    top: 3300px;
    right: 0;
  }
  &__container {
    position: relative;
    z-index: 20;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    position: relative;
    max-width: 551px;
    font-family: "Orelega";
    font-weight: 400;
    text-align: center;
    font-size: 69px;
    line-height: 1.1;
    color: var(--main-green);
    margin: 0 0 72px 0;
    &::after {
      content: "";
      position: absolute;
      bottom: -32px;
      left: 50%;
      transform: translateX(-50%);
      display: block;
      width: 271px;
      height: 10px;
      background-image: url("../img/wave.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
    @include mobile {
      font-size: 49px;
      margin: 0 0 56px 0;
    }
  }
  &__subtitle {
    font-family: "Orelega", serif;
    font-weight: 400;
    font-size: 48px;
    line-height: 1.1;
    color: var(--second-green);
    margin: 0 0 60px 0;
    @include mobile {
      font-size: 32px;
      margin: 0 0 32px 0;
    }
  }
}
