.shopcart {
  position: absolute;
  top: 450px;
  right: 1%;
  z-index: 2000;
  @include tablet {
    position: fixed;
    right: 0;
    top: 70px;
    overflow-y: auto;
    width: 345px;
    height: 80%;
    border-radius: 20px;
    padding: 30px 20px 32px;
    background-color: var(--main-green);
    transform: translateX(110%);
    transition: transform 0.3s ease-in-out;
    border: 2px solid var(--main-green);
  }

  // .shopcart__wrapper

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 296px;
    border: 2px solid var(--main-green);
    border-radius: 24px;
    padding: 24px;
    background-color: var(--light-color);
    @include tablet {
    }
  }

  // .shopcart__title

  &__title {
    font-family: "Orelega", serif;
    font-weight: 400;
    font-size: 26px;
    line-height: 1.1;
    color: var(--second-green);
    margin: 0 0 24px 0;
  }

  // .shopcart__list

  &__list {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 0 48px 0;
    &::after {
      content: "";
      position: absolute;
      bottom: -32px;
      left: 50%;
      transform: translateX(-50%);
      display: block;
      width: 248px;
      height: 10px;
      background-image: url("../img/wave.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
  &__cart-empty {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    //font-weight: 700;
    width: 100%;
    height: 49px;
    font-size: 16px;
    margin: 0 0 8px 0;
    color: var(--light-color);
    padding: 12px 15px;
    border-radius: 16px;
    background-color: var(--main-green);
    &.none {
      display: none;
    }
  }
  // .shopcart__item

  &__item {
    display: flex;
    &:not(:last-child) {
      margin: 0 0 16px 0;
    }
  }

  // .shopcart__item-del

  &__item-del {
    width: 24px;
    height: 24px;
    margin: 0 16px 0 0;
    cursor: pointer;
    transition: transform 0.3s ease-in-out;
    &:hover {
      transform: translateY(-2px);
    }
  }

  // .shopcart__item-box

  &__item-box {
  }

  // .shopcart__item-title

  &__item-title {
    font-weight: 700;
    font-size: 16px;
    margin: 0 0 8px 0;
    color: var(--second-green);
  }

  // .shopcart__item-quantity

  &__item-quantity {
    font-size: 16px;
    margin: 0;
    color: var(--second-green);
  }

  // .shopcart__total

  &__total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 0 0 24px 0;
  }

  // .shopcart__total-title

  &__total-title {
    font-weight: 700;
    font-size: 18px;
    color: var(--second-green);
    margin: 0;
  }

  // .shopcart__total-sum

  &__total-sum {
    font-size: 18px;
    color: var(--second-green);
    margin: 0;
  }

  // .shopcart__btns

  &__btns {
    display: flex;
    gap: 20px;
  }

  // .shopcart__sell

  &__sell {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 114px;
    height: 49px;
    background-color: var(--main-green);
    color: #fff;
    border-radius: 16px;
    font-size: 18px;
    transition: box-shadow 0.3s ease-in-out;
    &:hover {
      box-shadow: 0 0 10px var(--second-green);
    }
  }

  // .shopcart__cart-link

  &__cart-link {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 114px;
    height: 49px;
    background-color: var(--light-green);
    color: #343434;
    border: 1px solid #343434;
    border-radius: 16px;
    font-size: 18px;
    transition: box-shadow 0.3s ease-in-out;
    &:hover {
      box-shadow: 0 0 10px var(--second-green);
    }
  }
}
.cart--active {
  transform: translateX(0%);
}
