:root {
  // base
  --font-family: "Nunito", sans-serif;
  --content-width: 1610px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --main-text: #000;
  --main-green: #8cc63f;
  --second-green: #2f4f3c;
}
