.tabs {
  &__nav-box {
    display: flex;
    justify-content: center;
  }

  // .tabs__nav

  &__nav {
    position: relative;
    display: inline-flex;
    justify-content: center;
    margin: 0 0 84px 0;

    &::before {
      content: "";
      position: absolute;
      bottom: -12px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: var(--main-green);
      z-index: 20;
    }
    @media (max-width: 480px) {
      margin: 0 0 39px 0;
    }
  }

  // .tabs__nav-item

  &__nav-item {
    margin: 0;
    &:not(:last-child) {
      margin: 0 44px 0 0;
    }
    @media (max-width: 480px) {
      &:not(:last-child) {
        margin: 0 25px 0 0;
      }
    }
  }

  // .tabs__nav-btn

  &__nav-btn {
    position: relative;
    font-size: 18px;
    font-weight: 600;
    color: var(--second-green);
    padding: 5px;
    &::before {
      content: "";
      position: absolute;
      bottom: -12px;
      left: 0;
      width: 100%;
      height: 4px;
      background-color: var(--main-green);
      z-index: 20;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }
    &.active {
      &::before {
        opacity: 1;
      }
    }
  }

  // .tabs__content

  &__content {
    min-height: 750px;
    padding: 0 326px 0 0;
    @include tablet {
      padding: 0;
    }
  }
  &__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 32px;
    @include tablet {
      gap: 20px;
    }
  }
  &__box {
    display: block;
    &.hide {
      display: none;
    }
  }
}
