.header {
  position: relative;
  height: 46px;

  &__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: var(--main-green);
    box-shadow: 0px 4px 20px rgba(184, 180, 176, 0.8);
    z-index: 21;
  }
  // .header__logo-mobile
  &__logo-mobile {
    display: none;
    @include tablet {
      position: relative;
      display: block;
      width: 76px;
      height: 76px;
      top: 20px;
    }
  }

  // .header__cart-mobile

  &__cart-mobile {
    display: none;
    @include tablet {
      position: relative;
      display: block;
      width: 48px;
      height: 48px;
      z-index: 1000;
      span {
        position: absolute;
        top: -12px;
        right: -12px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        background-color: var(--second-green);
        color: var(--main-green);
        width: 40px;
        height: 40px;
        font-size: 22px;
        font-weight: 700;
        line-height: 1;
        border-radius: 50%;
      }
    }
  }

  // .header__container

  &__container {
    @include tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 4px 40px;
    }
  }
}

.burger {
  @include burger;
}
.nav {
  display: flex;
  justify-content: center;

  @include tablet {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 445px;
    border-radius: 20px;
    padding: 104px 20px 32px;
    background-color: var(--main-green);
    transform: translateY(-110%);
    transition: transform 0.3s ease-in-out;
  }
  // .nav__list

  &__list {
    display: flex;
    justify-content: center;
    align-items: center;

    @include tablet {
      flex-direction: column;
      width: 100%;
      height: 100%;
    }
  }

  // .nav__item

  &__item {
    margin: 0;
    &:not(:last-child) {
      margin: 0 44px 0 0;
    }
    @include tablet {
      &:not(:last-child) {
        margin: 0 0 36px 0;
      }
      &.nav__lang {
        margin: 0;
      }
    }
  }
  &__line {
    &::before {
      content: "";
      position: absolute;
      bottom: -5px;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #fff;
      z-index: 20;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }
    &:hover {
      &::before {
        opacity: 1;
      }
    }
  }
  // .nav__link

  &__link {
    position: relative;
    font-weight: 700;
    font-size: 18px;
    color: #fff;
    transition: color 0.3s ease-in-out;

    @media (min-width: 1025px) {
      &:hover {
        color: var(--second-green);
      }
      @include tablet {
        font-size: 24px;
      }
    }
  }
  &__lang {
    display: flex;
    align-items: center;
    &::after {
      content: "";
      display: block;
      width: 24px;
      height: 17px;
      background-image: url("../img/flag.png");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      z-index: 20;
      margin: 0 0 0 5px;
    }
  }

  // .nav__desktop

  &__desktop {
    @include tablet {
      display: none !important;
    }
  }
  &__logo {
    position: relative;
    top: 28px;
    display: block;
    width: 112px;
    height: 112px;
  }
}

.menu--active {
  transform: translateY(0%);
}
