.footer {
  position: relative;
  z-index: 10;
  background-color: var(--main-green);
  // .footer__content

  &__content {
    max-width: 1130px;
    margin: 0 auto;
    padding: 74px 15px 56px 15px;
  }

  // .footer__body

  &__body {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
  }

  // .footer__column

  &__column {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 10px;
    &:not(:last-child) {
      margin: 0px 10px 40px 10px;
    }
    @include tablet {
      width: 45%;
      align-items: center;
    }
    @media (max-width: 480px) {
      width: 100%;
    }
  }

  // .footer__title

  &__title {
    font-weight: 800;
    text-transform: uppercase;
    font-size: 18px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #fff;
    margin: 0 0 20px 0;
  }

  // .footer__link

  &__link {
    @media (any-hover: hover) {
      &:hover {
        text-decoration: underline;
      }
    }
  }

  // .footer__item

  &__item {
    font-weight: 600;
    font-size: 18px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #fff;
    margin: 0;
    &:not(:last-child) {
      margin: 0 0 20px 0;
    }
  }
}
