.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 296px;
  height: 439px;
  border-radius: 24px;
  padding: 24px;
  background-color: var(--light-color);
  box-shadow: 0 0 16px rgba(184, 180, 176, 0.5);
  transition: box-shadow 0.3s ease-in-out;
  &:hover {
    box-shadow: 0 0 20px rgba(184, 180, 176, 0.9);
  }
  @include tablet {
    width: 210px;
    height: 432px;
  }
  @media (max-width: 480px) {
    width: 100%;
    height: 400px;
  }
  &__title {
    font-family: "Orelega", serif;
    font-weight: 400;
    font-size: 26px;
    line-height: 1.1;
    color: var(--second-green);
    margin: 0 0 auto 0;
    @include tablet {
      font-size: 20px;
    }
  }
  &__image {
    width: 248px;
    height: 171px;
    margin: 20px 0 20px 0;
    img {
      width: 100%;
    }
    @include tablet {
      width: 178px;
      height: 104px;
    }
  }
  &__box-price {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 100%;
    margin: 0 0 20px 0;
    @include tablet {
      flex-direction: column;
      align-items: center;
    }
    @media (max-width: 480px) {
      flex-direction: row;
      justify-content: center;
      margin: 0 0 10px 0;
    }
  }
  &__price-new {
    margin: 0 10px 0 0;
    span {
      font-weight: 700;
      font-size: 22px;
      line-height: 1;
      color: #006837;
    }
    @include tablet {
      margin: 0 10px 2px 0;
    }
  }
  &__price-old-box {
    display: flex;
  }
  &__price-old {
    padding: 0 5px;
    margin: 0 20px 0 0;
    span {
      position: relative;
      font-size: 18px;
      line-height: 1;
      color: #000;
      z-index: 5;
    }
  }
  &__volume {
    span {
      font-size: 18px;
      line-height: 1;
      color: #000;
    }
  }
  &__box-button {
    display: flex;
    width: 100%;
    @include tablet {
      flex-direction: column;
    }
  }
  &__btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 114px;
    height: 49px;
    border-radius: 16px;
    background-color: var(--second-green);
    color: #fff;
    padding: 8px;
    font-size: 18px;
    font-weight: 700;
    line-height: 1;
    transition: box-shadow 0.3s ease-in-out;
    &:hover {
      box-shadow: 0 0 10px var(--second-green);
    }
    @include tablet {
      width: 100%;
    }
  }
  &__counter-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 114px;
    height: 49px;
    border-radius: 16px;
    border: 1px solid var(--second-green);
    margin: 0 20px 0 0;
    padding: 5px;
    transition: box-shadow 0.3s ease-in-out;
    &:hover {
      box-shadow: 0 0 10px var(--second-green);
    }
    @include tablet {
      width: 100%;
      margin: 0 0 12px 0;
    }
  }
  &__counter-control {
    padding: 5px 10px;
    font-size: 22px;
    color: #343434;
  }
  &__counter-current {
    font-size: 18px;
    color: #343434;
    padding: 0 5px;
  }
}

.old {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    top: 33%;
    left: 0;
    width: 100%;
    height: 3px;
    border-radius: 3px;
    background-color: #f47676;
    z-index: 1;
  }
}
